.sidebar{
    flex: 3.5;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: sticky;
    top:50px;
}

::-webkit-scrollbar{
    width: 5px;
}

::-webkit-scrollbar-track{
    background-color:white;
}

::-webkit-scrollbar-thumb{
    background:gray;
}


.sidebarWrapper{
    padding:20px;
}

.sidebarList{
    padding:0;
    margin:0;
    list-style: none;
}

.sidebarListItem{
    display:flex; 
    align-items: center;
    margin-bottom: 20px;
}

.sidebarIcon{
    margin-right: 15px;
}

.sidebarButton{
    width:150px;
    border:none;
    padding:10px;
    border-radius: 5px;
    font-weight: 500;
}

.sidebarHr{
     margin: 20px 0;
}
.sidebarFriendListHeader{
    margin-bottom: 20px ;
}
.sidebarFriendList{
    padding:0;
    margin:0;
    list-style: none;
}


